<template>
  <div id="app">
    <div class="mx-auto mt-4" style="width: 600px;">
      <h5 class="display-4">DEVELOPED BY IOWND</h5>
    </div>
    <b-container class="mt-4">
      <div>
        <b-button
          class="mx-1 button"
          v-for="profesion of profesiones"
          @dragstart="dragStart"
          @dragover.stop
          @click="addLast(profesion)"
          :draggable="true"
          :id="profesion.id"
          :variant="profesion.type"
          :key="profesion.id"
          v-show="profesion.usable"
          >{{ profesion.name }}
        </b-button>
      </div>
    </b-container>

    <div class="mx-auto" style="width: 500px;">
      <b-container class="mt-3">
        <b-form inline @submit.prevent="addPlayer">
          <b-input
            id="name"
            autofocus
            class="mb-2 mr-sm-2 mb-sm-0"
            placeholder="Jugador"
            v-model="player"
            @keyup.enter="addPlayer"
          ></b-input>
          <b-button variant="primary" @click="addPlayer">Agregar</b-button>
          <b-button variant="danger" class="ml-1" @click="clean"
            >Borrar Todos</b-button
          >
        </b-form>
      </b-container>
    </div>

    <b-container fluid class="mt-5">
      <b-row>
        <b-col sm="2" v-for="player of players" :key="player.id">
          <div>
            <b-card
              :title="player.name"
              style="max-width: 20rem; color: white; text-align:center;"
              class="mb-2 bg-secondary "
            >
              <hr />
              <div
                class="drop-area"
                @dragover.prevent
                @drop.prevent="drop"
                :id="player.id"
              >
                <b-button
                  class="m-1"
                  v-for="go of player.goTo"
                  :key="go.id"
                  :variant="go.type"
                  @click="deleteGoTo(player.id, go)"
                >
                  {{ go.name }}
                </b-button>
              </div>
            </b-card>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
const [TANK, BUFFER, DEBUFFER, DD] = ["warning ", "success", "dark", "danger"];

export default {
  name: "App",
  mounted() {
    (this.player = null), (this.onDrag = null), (this.players = []);
  },
  data() {
    return {
      player: null,
      onDrag: null,
      profesiones: [
        { id: 1, name: "TITAN", type: DD, usable: true },
        { id: 2, name: "GK", type: DD, usable: true },
        { id: 3, name: "CC", type: BUFFER, usable: true },
        { id: 4, name: "COV", type: BUFFER, usable: true },
        { id: 5, name: "APPETITE", type: BUFFER, usable: true },
        { id: 6, name: "GATO", type: BUFFER, usable: true },
        { id: 7, name: "SWM", type: BUFFER, usable: true },
        { id: 8, name: "SD", type: BUFFER, usable: true },
        { id: 9, name: "SEMILLA NEGRA", type: TANK, usable: true },
        { id: 10, name: "SEMILLA BLANCA", type: TANK, usable: true },
        { id: 11, name: "STIGMA", type: DEBUFFER, usable: true },
        { id: 12, name: "NECRO", type: DEBUFFER, usable: true },
        { id: 13, name: "LIMIT", type: DEBUFFER, usable: true }
      ],
      players: []
    };
  },
  methods: {
    addLast(profesion) {
      const playersLength = this.players.length;
      if (!playersLength) return;
      this.players[playersLength - 1].goTo.push(profesion);
      profesion.usable = false;
    },
    addPlayer() {
      if (this.player === "") return;
      this.players.push({
        id: Date.now(),
        name: this.player.toUpperCase(),
        goTo: []
      });
      this.player = "";
    },
    clean() {
      this.players = [];
      for (const profesion of this.profesiones) profesion.usable = true;
    },
    drop(e) {
      const dropId = e.target.id;
      const profesionId = this.onDrag;
      const profesion = this.profesiones.find(
        element => element.id == profesionId
      );
      profesion.usable = false;
      this.players.find(user => user.id == dropId).goTo.push(profesion);
    },
    dragStart(e) {
      this.onDrag = e.target.id;
    },
    deleteGoTo(playerId, go) {
      const indexProfesionToDelete = this.players
        .find(player => player.id == playerId)
        .goTo.indexOf(go);

      this.players
        .find(player => player.id == playerId)
        .goTo.splice(indexProfesionToDelete, 1);

      this.profesiones.find(profesion => profesion.id == go.id).usable = true;
    }
  }
};
</script>

<style>
body {
  background: #2c3e50; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #4ca1af,
    #2c3e50
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #4ca1af,
    #2c3e50
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.drop-area {
  /* display: flex;
  flex-direction: column; */
  width: 100%;
  max-width: 900px;
  padding: 15px;
  height: 40vh;
}
